import {ActionHandlerManager, StateActionHandler, ActionHandler} from '../utils/actions'
import {GetTradeList, GetTradeDetail, EditingTrade} from '../actions/tradesActions'

const handlerManager = new ActionHandlerManager();

const initState = {
  loadingState: 'loading', //加载状态. idle|over|error
  year: new Date().getFullYear(),
  month: new Date().getMonth(),
  monthIncome: null,
  monthExpense: null,
  monthSurplus: null, //本月结余
  monthInvest: null,  //本月理财收益
  totalCount:null,
  pageIndex:0,
  pageSize:0,
  trades: null,       //记账流水
  loadErrorMsg:null,

  details:{},  //详情信息

  editingTrade:null    //当前正在编辑的流水,当tallyId为0时，表示是新增
};

export default function tradesReducer(state = initState, action) {
  return handlerManager.dispatch(state, action);
}


/**
 * 加载流水列表
 */
handlerManager.addHandler(new StateActionHandler(GetTradeList, {
  onStart(state, action){
    let ns = Object.assign({}, state);
    ns.year = action.year;
    ns.month = action.month;
    ns.pageIndex = action.pageIndex;
    ns.spageSize = action.pageSize;
    ns.loadingState = 'loading';
    return ns;
  },
  onSucceed(state, action){
    let ns = Object.assign({}, state);
    ns.loadingState = 'idea';
    ns.year = action.year;
    ns.month = action.month;
    ns.monthIncome = action.monthIncome;
    ns.monthExpense = action.monthExpense;
    ns.monthSurplus = action.monthSurplus;
    ns.monthInvest = action.monthInvest;
    ns.totalCount = action.totalCount;
    ns.pageIndex = action.pageIndex;
    ns.spageSize = action.pageSize;

    if(ns.pageIndex == 1){
      ns.trades = [];
    }

    if(action.tallyList && action.tallyList.length > 0){
      ns.trades = Array.from([...(ns.trades||[]), ...action.tallyList]);
    }

    //判断是否已经全部加载完成
    if(ns.trades && ns.trades.length >= ns.totalCount){
      ns.loadingState = 'over'
    }
    return ns;
  },
  onError(state, action){
    let ns = Object.assign({}, state);
    ns.loadingState = 'error';
    ns.year = action.year;
    ns.month = action.month;
    ns.pageIndex = action.pageIndex;
    ns.spageSize = action.pageSize;
    ns.loadErrorMsg = action.error;
    return ns;
  }
}));

handlerManager.addHandler(new StateActionHandler(GetTradeDetail, {
  onSucceed(state, action){
    let ns = Object.assign({}, state);
    let trade  = action.trade;
    ns.details[trade.tallyId] = trade;
    ns.details = Object.assign({}, ns.details);
    return ns;
  }
}));

handlerManager.addHandler(new ActionHandler(EditingTrade, {
  handle(state, action){
    let ns = Object.assign({}, state);
    ns.editingTrade = action.trade;
    return ns;
  }
}));


