import {Toast} from 'antd-mobile'

const loadingProgress = {
  success (content="加载成功!", duration, onClose) {
    Toast.hide();
    Toast.success(content, duration, onClose);
  },
  fail (content='加载失败!', duration, onClose) {
    Toast.hide();
    Toast.fail(content, duration, onClose);
  },
  info (content='加载完成', duration, onClose) {
    Toast.hide();
    Toast.info(content, duration, onClose);
  },
  loading (content='加载中...', duration=90000, onClose) {
    Toast.hide();
    Toast.loading(content, duration, onClose);
  },
  offline (content='网络连接失败', duration, onClose) {
    Toast.hide();
    Toast.offline(content, duration, onClose);
  },
  hide(){
    Toast.hide();
  }

};


export default loadingProgress;


