import {STATIC_IMG_URL} from '../config'

/**
 * 收支类型
 * @type {{INCOME: number, OUTLAY: number}}
 */
export const IOType = {
  INCOME: 1, //收入
  OUTLAY: 2, //支出
};

/**
 * 记账来源
 * @type {{DOING: string, OVER: string, TIMEOUT: string}}
 */
export const TallyChannels = {
  MANUAL_TALLY: {id: 1, name: "手工记账"},
  CREDIT_CONSUMPTION: {id: 2, name: "信用卡消费"},
  SMS: {id: 3, name: "短信"},
  TRADE_CUSTOMER: {id: 4, name: "会员交易"},
  JD_ORDER: {id: 5, name: "京东订单"},
  VAULT_INCOME: {id: 6, name: "小金库收益"},
  FUND_INCOME: {id: 7, name: "基金收益"},
  CYCLE_TALLY: {id: 8, name: "周期记账"},
  WANGC_INCOME: {id: 9, name: "旺财收益"},
  REGULAR_FINACING_INCOME: {id: 10, name: "定期理财收益"},

  getNameById(id = 1){
    for (let key in TallyChannels) {
      if (TallyChannels[key] && TallyChannels[key].id == id) {
        return TallyChannels[key].name;
      }
    }
  }
};


export const TypeDefaultImage = STATIC_IMG_URL + '/tally_ic_default_type.png';



